<i18n>
{
  "en": {
    "平台设计师": "Yogooooo Designers",
    "查看主页": "View Portfolio",
    "设计师作品": "Designer works",
    "寻找项目": "Find Projects",
    "设计费": "Consulting fee",
    "新发布": "New released",
    "进行中": "In progress",
    "已完成": "Completed",
    "查看所有项目": "View All Projects",
    "前往设计师广场": "Go To Designers' Plaza"
  }
}
</i18n>

<template>
  <div class="page-guide">
    <el-carousel
      v-loading="loading_w"
      element-loading-text="Loading"
      element-loading-background="transparent"
      trigger="click"
      height="500px"
      arrow="always"
      indicator-position="none">
      <el-carousel-item
        v-for="work in works"
        :key="work.id"
        :work="work"
        class="work-list-item">
        <img
          :src="work.url"
          class="work-list-item__img">
        <div class="work-list-item__title-container">
          <div class="work-list-item__title-area">
            <div
              v-if="work.author"
              class="work-list-item__name">
              <i class="el-icon-caret-right mr1" />{{ work.author }}
            </div>
            <div class="work-list-item__title">
              {{ work.title }}
            </div>
            <router-link to="/square?type=work">
              <el-button
                type="default"
                size="large"
                round
                plain>{{ $t('前往设计师广场') }}</el-button>
            </router-link>
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
    <div class="main-container">
      <div
        v-loading="loading_d"
        element-loading-background="transparent"
        class="designer-card">
        <h2
          v-t="'平台设计师'"
          class="designer-card__title"/>
        <el-scrollbar>
          <ul class="list-reset designer-list">
            <li
              v-for="designer in designers"
              :key="designer.id"
              class="designer-list-item">
              <router-link :to="'profile?uid=' + designer.id">
                <my-avatar
                  :avatar-url="designer.avatar_url"
                  class="designer-list-item__avatar"/>
              </router-link>
              <router-link :to="'profile?uid=' + designer.id">
                <h3 class="m0 mt1 mb1 f-13 ellipsis-1 color-primary">{{ designer.name }}</h3>
              </router-link>
              <p class="m0 f-12 black-65 ellipsis-1">{{ designer.title || $t('设计师') }}</p>
              <router-link :to="'profile?uid=' + designer.id">
                <el-button
                  type="primary"
                  size="large">{{ $t('查看主页') }}</el-button>
              </router-link>
            </li>
          </ul>
        </el-scrollbar>
      </div>
      <el-card
        v-loading="loading_p"
        element-loading-background="transparent"
        shadow="hover"
        class="mt-24 project-card">
        <div slot="header">
          <img
            style="width:auto; height:25px; margin-right:4px; vertical-align:middle;"
            src="@/assets/logo_primary.png"
          >
          <span style="vertical-align: middle;">{{ $t('寻找项目') }}</span>
        </div>
        <ul class="project-list">
          <li
            v-for="project in projects"
            :key="project.id"
            class="project-list-item">
            <el-tag
              v-if="isNewReleased(project)"
              type="success"
              class="mr1">{{ $t('新发布') }}</el-tag>
            <el-tag
              v-else-if="isInProgress(project)"
              class="mr1">{{ $t('进行中') }}</el-tag>
            <el-tag
              v-else
              type="info"
              class="mr1">{{ $t('已完成') }}</el-tag>
            <router-link
              :to="`/project/${project.id}`"
              class="project-list-item__title mr1"
              v-text="project.title"
            />
            <span
              class="black-65 flex-none"
              v-text="project.payment"/>
          </li>
        </ul>
      </el-card>
      <router-link
        to="/search"
        class="block mt2 center">
        <el-button
          type="primary"
          size="large">{{ $t('查看所有项目') }}</el-button>
      </router-link>
    </div>
  </div>
</template>

<script>
import { Project } from '@/services/constants'
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['works', 'designers', 'projects']),
    loading_w () {
      return this.$store.state.index.loading_w
    },
    loading_p () {
      return this.$store.state.index.loading_p
    },
    loading_d () {
      return this.$store.state.index.loading_d
    }
  },
  created () {
    this.$store.dispatch('getIndexWorks')
    this.$store.dispatch('getIndexProjects')
    this.$store.dispatch('getIndexDesigners')
  },
  methods: {
    isNewReleased (project) {
      // eslint-disable-next-line
      return project.status == Project.STATUS_TENDERING;
    },
    isInProgress (project) {
      // eslint-disable-next-line
      return project.status == Project.STATUS_WORKING;
    }
  }
}
</script>

<style lang="scss">
.page-guide {
  .el-carousel__arrow {
    left: auto;
    top: auto;
    bottom: 16px !important;
    background-color: rgba(#305497, 0.45);
    &:hover {
      background-color: rgba(#305497, 0.85);
    }
  }
  .el-carousel__arrow--left {
    right: 84px !important;
  }
  .el-carousel__arrow--right {
    right: 40px !important;
  }

  .project-card {
    padding-left: 24px;
    padding-right: 24px;
    background-color: rgba(#305497, 0.05);
    .el-card__header {
      color: #305497;
      border-bottom-color: rgba(#305497, 0.25);
    }
    & > * {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
</style>

<style lang="scss" scoped>
.work-list-item {
  &__img {
    width: 100%;
  }
  &__title-container {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    background: rgba(0, 0, 0, 0.1);
    .work-list-item__title-area {
      position: absolute;
      left: 48px;
      bottom: 80px;
      width: 45%;
      & > * {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 400;
        line-height: 1.5em;
      }
      .work-list-item__name {
        font-size: 16px;
      }
      .work-list-item__title {
        font-size: 30px;
      }
      .el-button {
        margin-top: 8px;
        background: transparent;
        color: #fff;
        &:hover,
        &:focus {
          color: #305497;
        }
      }
    }
  }
}
.designer-card {
  margin: 24px 0 56px;
  &__title {
    text-align: center;
    color: #305497;
    font-weight: normal;
  }
}
.designer-list {
  white-space: nowrap;
  margin: 0;
  height: 210px; // 185+20
  &-item {
    position: relative;
    display: inline-block;
    width: 160px;
    height: 185px;
    padding: 16px 16px 32px;
    margin-right: 8px;
    text-align: center;
    border: 1px solid rgba(#305497, 0.3);
    border-radius: 4px;
    background-color: rgba(#305497, 0.05);
    box-shadow: 0px 0px 3px rgba(#305497, 0.3);
    &:last-child {
      margin-right: 0;
    }
    &__avatar {
      width: 80px;
      height: 80px;
    }
    .el-button {
      position: absolute;
      left: 50%;
      bottom: -20px;
      transform: translate(-50%);
    }
  }
}
.project-list {
  margin: 0;
  padding: 0;
  list-style-type: circle;
  &-item {
    display: flex;
    margin-bottom: 8px;
    &__title {
      flex: 1;
      font-size: 16px;
      cursor: pointer;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>



